    padding: 0;
    margin: 0;
  }
   
  body {
    font-family: 'Poppins', sans-serif;
    background-color: white;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }
  #skip-to-content {
    position: absolute;
    top: -70px;
    left: 0;
    padding: 22px;
    background-color: #fc5603;
    border-radius: 10px;
    color: #fff;
    text-decoration: none;
    z-index: 999;
  }
  #skip-to-content:focus {
    top: 0;
  }
   
  /*
    AppBar
  */
   
  .app-bar {
    background-image: linear-gradient(to bottom right, #D67D3E, #EE9322);
    padding: 8px 16px;
    background-color: white;
    display: grid;
    grid-template-columns: auto 1fr auto;
    gap: 10px;
    position: sticky;
    top: 0;
    z-index: 99;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  }
   
  .app-bar .app-bar__menu {
    display: flex;
    align-items: center;
  }
   
  .app-bar .app-bar__menu button {
    background-color: transparent;
    border: none;
    font-size: 18px;
    padding: 8px;
    cursor: pointer;
  }
   
  .app-bar .app-bar__brand {
    display: flex;
    align-items: center;
  }
   
  .app-bar .app-bar__brand h1 {
    color: #ffff;
    text-transform: uppercase;
    font-size: 22px;
    user-select: none;
  }
  #hamburgerButton{
    color: #ffff;
    font-size: 24px;
    box-sizing: border-box;
    min-width: 44px;
    min-height: 44px;
  }
   
  .app-bar .app-bar__navigation {
    position: absolute;
    top: 50px;
    left: -180px;
    width: 150px;
    transition: all 0.3s;
    padding: 8px;
    background-color: white;
    overflow: hidden;
  }
   
  .app-bar .app-bar__navigation.open {
    left: 0;
  }
   
  .app-bar .app-bar__navigation ul li a {
    display: inline-block;
    text-decoration: none;
    color: black;
    padding: 16px;
    margin-bottom: 5px;
    width: 100%;
  }
   
  /*
    Main Content
  */
   
  main {
    flex: 1;
  }

  .wrapper-jumbotron {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    padding: 0 0 32px 0;
  }
  .wrapper-jumbotron img{
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  } 
  
  .content h2 {
    margin: 0 auto;
    min-height: 100%;
    text-align: center;
  }
  .content p {
    margin: 0 auto;
    min-height: 100%;
    text-align: center;
  }
   
  .content .content__heading {
    font-weight: normal;
  }
   
   
  /*
    Restaurants
  */
   
  .restaurants {
    display: grid;
    grid-template-columns: 1fr;
    gap: 16px;
    margin: 32px 0;
  }
   
   
  /*
    Restaurant Item
  */
   
  .restaurant-item {
    width: 100%;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    border-radius: 8px;
    overflow: hidden;
  }
   
  .restaurant-item__header {
    position: relative;
  }
   
  .restaurant-item .restaurant-item__header .restaurant-item__header__poster {
    min-width: 400px;
    height: 200px;
    object-fit: cover;
    object-position: center;

  }
   
  .restaurant-item .restaurant-item__header .restaurant-item__header__rating {
    position: absolute;
    padding: 8px;
    bottom: 20px;
    left: 0;
    display: inline-block;
    background-color: black;
    color: white;
  }
   
  .restaurant-item .restaurant-item__header .restaurant-item__header__rating .restaurant-item__header__rating__score {
    margin-left: 10px;
  }
   
  .restaurant-item .restaurant-item__content {
    padding: 16px;
  }
   
  .restaurant-item .restaurant-item__content h3 {
    margin: 0 0 10px 0;
  }
   
  .restaurant-item .restaurant-item__content h3 a {
    color: #fc5603;
    text-decoration: none;
    padding:9px 0 9px 0;
  }
   
  .restaurant-item .restaurant-item__content p {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    text-align: start;
    -webkit-line-clamp: 4; /* number of lines to show */
    -webkit-box-orient: vertical;
  }
   
   
  /*
    Restaurant
  */
   
  .restaurant {
    display: flex;
    gap: 16px;
    margin: 32px 32px 32px 32px;
}

.restaurant__poster {
    width: 550px;

}

.restaurant__poster__image {
    width: 100%;
    height: 490px;
    object-fit: cover;
}

.restaurant__info {
    flex: 2;
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.restaurant__info__details {
    flex: 2;
}

.restaurant__info__menus {
    flex: 1;
    display: flex;
    gap: 32px;
}

.restaurant__info__menu {
    flex: 1;
}
   
/*
  Like
*/
.like {
    width: 55px;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fc5603;
    
    position: fixed;
    bottom: 16px;
    right: 16px;
    border-radius: 50%;
    border: 0;
    
    font-size: 18px;
    color: white;
    cursor: pointer;
  }
   
  /*
    Customer Review
  */
  .customerReviews-item{
    display: flex;
    flex-direction: column;
    gap: 8px;
    margin: 32px 32px 32px 32px;
    border: 2px solid #fc5603;
    padding: 32px 32px 32px 32px;
    border-radius: 10px;
  }
    .customerReviews-item__title h3{
        font-size: 24px;
        font-weight: bold;
        margin-left: 32px;
    }
    .customerReviews-item__header{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .customerReviews-item__name{
        font-weight: bold;
        font-size: 18px;
    }
    .customerReviews-item__date{
        color: #fc5603;
    }
    .customerReviews-item__review{
        margin: 0;
        font-size: 18px;
    }

    


  /*
    Footer
  */
   
  footer {
    padding: 16px;
  }
   
  footer p {
    text-align: center;
    color: #aaaaaa;
  }
   
  footer p a {
    color: #D67D3E;
    text-decoration: none;
    padding: 16px 0 16px 0;
  }`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;IACI,UAAU;IACV,SAAS;EACX;;EAEA;IACE,kCAAkC;IAClC,uBAAuB;IACvB,iBAAiB;IACjB,aAAa;IACb,sBAAsB;EACxB;EACA;IACE,kBAAkB;IAClB,UAAU;IACV,OAAO;IACP,aAAa;IACb,yBAAyB;IACzB,mBAAmB;IACnB,WAAW;IACX,qBAAqB;IACrB,YAAY;EACd;EACA;IACE,MAAM;EACR;;EAEA;;GAEC;;EAED;IACE,oEAAoE;IACpE,iBAAiB;IACjB,uBAAuB;IACvB,aAAa;IACb,oCAAoC;IACpC,SAAS;IACT,gBAAgB;IAChB,MAAM;IACN,WAAW;IACX,0CAA0C;EAC5C;;EAEA;IACE,aAAa;IACb,mBAAmB;EACrB;;EAEA;IACE,6BAA6B;IAC7B,YAAY;IACZ,eAAe;IACf,YAAY;IACZ,eAAe;EACjB;;EAEA;IACE,aAAa;IACb,mBAAmB;EACrB;;EAEA;IACE,YAAY;IACZ,yBAAyB;IACzB,eAAe;IACf,iBAAiB;EACnB;EACA;IACE,YAAY;IACZ,eAAe;IACf,sBAAsB;IACtB,eAAe;IACf,gBAAgB;EAClB;;EAEA;IACE,kBAAkB;IAClB,SAAS;IACT,YAAY;IACZ,YAAY;IACZ,oBAAoB;IACpB,YAAY;IACZ,uBAAuB;IACvB,gBAAgB;EAClB;;EAEA;IACE,OAAO;EACT;;EAEA;IACE,qBAAqB;IACrB,qBAAqB;IACrB,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,WAAW;EACb;;EAEA;;GAEC;;EAED;IACE,OAAO;EACT;;EAEA;IACE,aAAa;IACb,0BAA0B;IAC1B,uBAAuB;IACvB,mBAAmB;EACrB;EACA;IACE,cAAc;IACd,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,uBAAuB;EACzB;;EAEA;IACE,cAAc;IACd,gBAAgB;IAChB,kBAAkB;EACpB;EACA;IACE,cAAc;IACd,gBAAgB;IAChB,kBAAkB;EACpB;;EAEA;IACE,mBAAmB;EACrB;;;EAGA;;GAEC;;EAED;IACE,aAAa;IACb,0BAA0B;IAC1B,SAAS;IACT,cAAc;EAChB;;;EAGA;;GAEC;;EAED;IACE,WAAW;IACX,0CAA0C;IAC1C,kBAAkB;IAClB,gBAAgB;EAClB;;EAEA;IACE,kBAAkB;EACpB;;EAEA;IACE,gBAAgB;IAChB,aAAa;IACb,iBAAiB;IACjB,uBAAuB;;EAEzB;;EAEA;IACE,kBAAkB;IAClB,YAAY;IACZ,YAAY;IACZ,OAAO;IACP,qBAAqB;IACrB,uBAAuB;IACvB,YAAY;EACd;;EAEA;IACE,iBAAiB;EACnB;;EAEA;IACE,aAAa;EACf;;EAEA;IACE,kBAAkB;EACpB;;EAEA;IACE,cAAc;IACd,qBAAqB;IACrB,mBAAmB;EACrB;;EAEA;IACE,gBAAgB;IAChB,uBAAuB;IACvB,oBAAoB;IACpB,iBAAiB;IACjB,qBAAqB,EAAE,4BAA4B;IACnD,4BAA4B;EAC9B;;;EAGA;;GAEC;;EAED;IACE,aAAa;IACb,SAAS;IACT,2BAA2B;AAC/B;;AAEA;IACI,YAAY;;AAEhB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,iBAAiB;AACrB;;AAEA;IACI,OAAO;IACP,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,OAAO;AACX;;AAEA;IACI,OAAO;IACP,aAAa;IACb,SAAS;AACb;;AAEA;IACI,OAAO;AACX;;A